import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';

import { connect } from "react-redux";
import "./styles.css";

class FooterComponent extends React.Component {
    constructor(props) {
        super(props);

    }
    updateCurrentLink = (data) => {
        this.props.setNavigation(data);
    }

    render() {
        return (
            <div className="footerContainer">
                <AppBar position="static">
                    <Toolbar>
                        <IconButton edge="start" color="inherit" aria-label="menu">
                            <MenuIcon />
                        </IconButton>
                        <div className="menuBtn">
                            <Button color="inherit" onClick={() => { this.updateCurrentLink("home") }}>Home</Button>
                            <Button color="inherit" onClick={() => { this.updateCurrentLink("about") }}>About</Button>
                            <Button color="inherit" onClick={() => { this.updateCurrentLink("skills") }}>Skills</Button>
                            <Button color="inherit" onClick={() => { this.updateCurrentLink("contactus") }}>Contact US</Button>
                        </div>
                    </Toolbar>
                </AppBar>
            </div>

        )
    }
}

const mapStateToProps = (state) => {
    return {
        currentLink: state && state.currentLink
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        setNavigation: (data) => { dispatch({ type: "SET_UP_NAV_LINK", value: data }) }
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(FooterComponent);