import React from 'react';

import {connect} from "react-redux";
import Img from "../assets/image1";
import ContentComponent from "./content";

class MainComponent extends React.Component {
    constructor(props){
        super(props);

        this.state = {
        };
    }

    render(){
        const values = {
            home:0,
            about:250,
            skills:800,
            contactus:1000
        };
        const value = values[this.props.currentLink];
        return (
            <div>
                <img style={{position:"absolute",top:-value,left:'3%',width:'94%',transition: 'top .5s ease-in-out'}} src={Img} />
                <div className="backGroundColor"></div>
                <ContentComponent {...this.props}/>
            </div>

        )
    }
} 

const mapStateToProps = (state) => {
    return {
        count: state && state.count,
        currentLink: state && state.currentLink

    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        up : () => { dispatch({type:"AGE_UP",value:1})},
        down : () => { dispatch({type:"AGE_DOWN",value:1})},
    }
}


export default connect(mapStateToProps,mapDispatchToProps)(MainComponent);