
import React from 'react';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { connect } from "react-redux";

class ContentComponent extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
        };
    }

    render() {
        const about1 = `data:image/png;base64,`;
        const about2 = `data:image/png;base64,`;
        const about3 = `data:image/png;base64,`;
        const about4 = `data:image/png;base64,`;
        return (
            <div className="content">

                {this.props.currentLink === 'home' && <Card>
                    <CardContent>
                        <Typography color="textSecondary" gutterBottom>
                            Hello, I'm <span class="highlight">Naga Kesava Rao</span>.
                         <br />
                        <span className="subContent"> I'm a Front-End Developer <br />I have serious passion for UI effects, animations and<br /> creating intuitive, dynamic user experiences. <br />Let's make something special.</span>
                    </Typography>
                    </CardContent>
                </Card>}
                {this.props.currentLink === 'about' &&<Card>
                    <CardContent>
                        <img src={about1} style={{width:'25%'}} />
                        <img src={about2} style={{width:'25%'}}/>
                        <img src={about3} style={{width:'21%'}}/>
                        <img src={about4} style={{width:'21%'}}/>
                    </CardContent>
                </Card>}

            </div>

        )
    }
}

const mapStateToProps = (state) => {
    return {
        count: state && state.count,
        currentLink: state && state.currentLink

    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        up: () => { dispatch({ type: "AGE_UP", value: 1 }) },
        down: () => { dispatch({ type: "AGE_DOWN", value: 1 }) },
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(ContentComponent);          